      </div>
    </div>
    -->
  
  </div>
</template>

<script>

export default {
  name: 'FindTheNumber',
  components: {
  },
  data() {
    return {
      targetNumber:[0,"textt"],
      numbersList:[],

      score:0,

      timeLimit: 60,
      timePassed: 0,
      timerInterval: null,
    
      stopGame:false,

      alert:{status:false,class:"opacity-0",color:null,msg:null},
    }
  },
  created() {
    this.callAddNumber();
  },
  methods: {
    callAddNumber(){
      //this.AddNumbers();
      this.randomAddNumbers();
    },
    clickNumber(number){
      var vm=this;
      if(vm.stopGame)return;
      if(!vm.timerInterval) vm.startTimer();

      if(vm.targetNumber[0] == number.num){
        vm.score += 1;
        vm.alertShow("+1")
        vm.callAddNumber();
      }else{
        if(vm.score > 0)vm.score -= 1;
        if(vm.score > 0)vm.alertShow("-1","bg-red-500")
        else vm.alertShow("0","bg-red-500")
        vm.callAddNumber();
      }
    },
    AddNumbers(){
      var vm = this;
      vm.numbersList = [];
      vm.targetNumber[0] = Math.floor(Math.random() * 18)
      vm.targetNumber[1] = "text-gray-900"
      for (let n = 0; n < 18; n++) {
        vm.numbersList.push({
          color:"text-gray-900",
          num:n
        });
      }
    },
    randomAddNumbers(){
      this.numbersList = [];
      for(var a=[],i=0;i<36;++i)a[i]=Math.floor(i/3);

      var temp , current , top = a.length;
      if(top) while (--top) {
        current = Math.floor(Math.random() * (top + 1));
        temp = a[current]
        a[current] = a[top];
        a[top] = temp;
      }

      this.targetNumber[0] = a[Math.floor(Math.random() * a.length)]
      this.targetNumber[1] = "text-gray-900"
      
      for (let n = 0; n < a.length; n++) {
        this.numbersList.push({
          color:"text-gray-900",
          num:a[n]
        });
      }
    },
    getRandomColor(){
      var a=["red","gray","green","blue","indigo","orange","yellow","purple","teal","pink"]
      var color = a[Math.floor(Math.random() * a.length)]
      return color;
    },

    getColor(){
      var vm = this;
      if(vm.timePassed == vm.timeLimit)return 'text-red-500'
      else if(vm.timePassed >= (vm.timeLimit/2))return 'text-yellow-500'
      else return 'text-green-500'
    },

    startTimer(){
      this.timerInterval = setInterval((() => this.timePassed += 1), 1000);
    },
    stopTimer(){
      clearInterval(this.timerInterval);
    },
    TimeOver(){
      this.stopGame = true;
      this.stopTimer();
      this.alertTimeOver();
      console.log("Time Over")
    },

    alertShow(msg,color = "bg-green-500"){
      var vm = this;
      vm.alert.status = true;
      vm.alert.class = "opacity-1"
      vm.alert.color = color;
      vm.alert.msg = msg;
      setTimeout(function(){
        vm.alertFadeOut();
      },500);
    },
    alertTimeOver(){
      var vm = this;
      vm.alert.status = true;
      vm.alert.class = "opacity-1"
      vm.alert.color = "bg-red-500";
      vm.alert.msg = "Time Over :(";
      setTimeout(function(){
        vm.alertFadeOut();
      },6000);
    },
    alertFadeOut(){
      var vm = this;
      vm.alert.class = "opacity-0"
      setTimeout(function() {
        vm.alert.status = false;
      }, 500);
    },
  },
  computed: {
    timer(){
      var timeLeft = this.timeLimit - this.timePassed
      if(!timeLeft)this.TimeOver();
      return timeLeft
    }
  },
}
</script>

<style>
.alertFade{
  -webkit-transition: opacity 0.5s ease-in-out;
  -moz-transition: opacity 0.5s ease-in-out;
  -ms-transition: opacity 0.5s ease-in-out;
  -o-transition: opacity 0.5s ease-in-out;
}
</style>
